.header {
    margin: auto;
}
@media screen and (min-width: 1281px) {
    .header {
        width: 880px;
    }
}
@media screen and (max-width: 1100px) {
    .header {
        width: 90%;
    }
}
@media screen and (max-width: 320px) {
    .header {
        width: 100%;
        padding-bottom: 31.6px;
    }
}
